  margin-left: 32px;
  margin-bottom: 12px;
}

@media screen and (max-width: 1099px) {
  .checkbox .wpcf7-list-item {
    margin-left: 24px;
  }
}

.checkbox input[type="checkbox"] {
  display: none;
}

.checkbox input[type="checkbox"] + span {
  cursor: pointer;
  display: inline-block;
  padding: 0 0 0 32px;
  position: relative;
  font-size: 14px;
}

.checkbox input[type="checkbox"] + span::before {
  -webkit-transform: translateY(-50%);
  background: #fff;
  border: 1px solid #1391E6;
  content: "";
  display: block;
  height: 24px;
  left: 0;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  width: 24px;
}

.checkbox input[type="checkbox"] + span::after {
  -webkit-transform: translateY(-50%) rotate(-45deg);
  border-bottom: 3px solid #1391E6;
  border-left: 3px solid #1391E6;
  content: "";
  display: block;
  height: 10px;
  left: 3px;
  margin-top: -0.2em;
  opacity: 0;
  position: absolute;
  top: 50%;
  transform: translateY(-50%) rotate(-45deg);
  transition: opacity 0.3s;
  width: 18px;
}

.checkbox input[type="checkbox"]:checked + span::after {
  opacity: 1;
}

.drawer-icon {
  position: fixed;
  top: 0;
  right: 0;
  padding: 22px 15px 7px 17px;
  z-index: 21;
  display: block;
  transition: transform 0.3s;
}

.drawer-icon.is-active {
  padding: 29px 12px 7px 17px;
}

.drawer-icon.is-active .drawer-icon__bar1 {
  transform: rotate(-45deg);
  background: #fff;
  top: 0;
}

.drawer-icon.is-active .drawer-icon__bar2 {
  display: none;
}

.drawer-icon.is-active .drawer-icon__bar3 {
  transform: rotate(45deg);
  background: #fff;
  top: 0;
}

.drawer-icon__bars {
  width: 32px;
  height: 32px;
  display: block;
  position: relative;
}

.drawer-icon__bar1,
.drawer-icon__bar2,
.drawer-icon__bar3 {
  transition: transform 0.3s;
  position: absolute;
  width: 27px;
  height: 2.25px;
  background: #1391E6;
  top: 0;
  left: 0;
  border-radius: 10px;
}

.drawer-icon__bar1 {
  top: 0;
}

.drawer-icon__bar2 {
  top: 7px;
}

.drawer-icon__bar3 {
  top: 14px;
}

.drawer-content {
  display: block;
  width: 100%;
  height: 100%;
  position: fixed;
  top: 59px;
  z-index: 20;
  transform: translateY(-110%);
  background: #0060A0;
  transition: transform 0.3s;
}

.drawer-content.is-active {
  transform: translateY(0);
}

.drawer-content__items {
  margin: 40px 60px 0;
}

.drawer-content__item {
  border-bottom: 1px dotted #fff;
  position: relative;
}

.drawer-content__item::before {
  position: absolute;
  content: "";
  background: url(../../../WordPressTheme/assets/img/arrow-right-white.svg);
  background-size: contain;
  top: 50%;
  right: 0;
  width: 14px;
  height: 14px;
  transform: translateY(-50%);
}

.drawer-content__item:first-child {
  border-top: 1px dotted #fff;
}

.drawer-content__link {
